import React,{useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import DirectionsIcon from '@material-ui/icons/Directions';
import {

  withRouter
} from "react-router-dom";
const useStyles = makeStyles(theme => ({
  root: {
    padding: '1px 3px',
    display: 'flex',
    alignItems: 'center',

  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
  },
  iconButton: {
    padding: 6,
  },
  divider: {
    height: 28,
    margin: 4,
  },
}));

 function CustomizedInputBase(props) {
  const classes = useStyles();
  const [text, setText] = useState(null);
  return (
    <Paper elevation={0} className={classes.root}>

      <InputBase
        fullWidth
      onChange={(e)=>{
        setText(e.target.value)
      }}
      onKeyDown={(e)=>{
        if (e.key === 'Enter'&& text!=null) {
          props.history.push('/products/search/'+text)
        }
      }}
        className={classes.input}
        placeholder="Search Products"
        inputProps={{ 'aria-label': 'search google maps' }}
      />
      <IconButton onClick={()=>{props.history.push('/products/search/'+text)}} className={classes.iconButton} aria-label="search">
        <SearchIcon />
      </IconButton>


    </Paper>
  );
}

export default withRouter(CustomizedInputBase)
